<template>
    <div>
        <!-- 面包屑导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>商品管理</el-breadcrumb-item>
          <el-breadcrumb-item>添加商品</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图 -->
        <el-card>
            <!-- 提示区域 -->
            <el-alert
            title="添加商品信息"
            type="info"
            show-icon
            center :closable="false">
            </el-alert>
            <!-- 进度条 -->
            <!-- :active="activeIndex - 0"转换为数字 -->
            <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
                <el-step title="基本信息"></el-step>
                <el-step title="商品参数"></el-step>
                <el-step title="商品属性"></el-step>
                <el-step title="商品图片"></el-step>
                <el-step title="商品内容"></el-step>
                <el-step title="完成"></el-step>
            </el-steps>
            <!-- tab区域 -->
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" label-position="top">
                <el-tabs v-model="activeIndex" tab-position="left" :before-leave="beforeTabLave" @tab-click="tabClicked">
                  <el-tab-pane label="基本信息" name="0">
                      <el-form-item label="商品名称" prop="goods_name">
                          <el-input v-model="addForm.goods_name"></el-input>
                      </el-form-item>
                      <el-form-item label="商品价格" prop="goods_price">
                          <el-input v-model="addForm.goods_price" type="number"></el-input>
                      </el-form-item>
                      <el-form-item label="商品重量" prop="goods_weight">
                          <el-input v-model="addForm.goods_weight" type="number"></el-input>
                      </el-form-item>
                      <el-form-item label="商品数量" prop="goods_number">
                          <el-input v-model="addForm.goods_number" type="number"></el-input>
                      </el-form-item>
                      <el-form-item label="商品分类" prop="goods_cat">
                          <el-cascader
                          v-model="addForm.goods_cat"
                          :options="cateList"
                          @change="handleChange"
                          :props="cateProps"></el-cascader>
                      </el-form-item>
                  </el-tab-pane>
                  <el-tab-pane label="商品参数" name="1">
                      <!-- 渲染表单的item项 -->
                      <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
                        <!-- 复选框 -->
                        <el-checkbox-group v-model="item.attr_vals">
                          <el-checkbox :label="item" v-for="(item, i) in item.attr_vals" :key="i" border></el-checkbox>
                        </el-checkbox-group>
                      </el-form-item>
                  </el-tab-pane>
                  <el-tab-pane label="商品属性" name="2">
                    <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
                      <el-input v-model="item.attr_vals">
                      </el-input>
                    </el-form-item>
                  </el-tab-pane>
                  <el-tab-pane label="商品图片" name="3">
                        <!-- action是上传的地址 -->
                    <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :headers="headersObj" :on-success="handleSuccess">
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                  </el-tab-pane>
                  <el-tab-pane label="商品内容" name="4">
                    <!-- 富文本编辑器 -->
                    <quill-editor v-model="addForm.goods_introduce"></quill-editor>
                    <!-- 添加商品的按钮 -->
                    <el-button type="primary" class="btnTop" @click="add">添加商品</el-button>
                  </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
        <!-- 图片预览对话框 -->
        <el-dialog
        title="图片预览"
        :visible.sync="previewdialogVisible"
        width="50%">
          <img :src="previewPath" class="preview">
        </el-dialog>
    </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      activeIndex: 0,
      // 添加商品的表单数据对象
      addForm: {
        goods_name: '',
        // 商品所属的分类数组
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_introduce: '',
        // 图片的额数组
        pics: [],
        // 商品的详情描述
        attrs: []
      },
      // 添加商品表单验证规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入活动名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'blur' }
        ]
      },
      // 商品分类列表
      cateList: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 动态参数列表
      manyTableData: [],
      // 静态属性列表
      onlyTableData: [],
      // 上传图片路径
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 图片上传组件的请求头对象
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 预览图片在的服务器路径
      previewPath: '',
      // 预览对话框显示
      previewdialogVisible: false
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 获取所有商分类数据
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('请求商品分类列表失败!')
      }
      this.cateList = res.data
    },
    // 级联选择器变化触发函数
    handleChange () {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // 监听tab页切换的钩子函数
    beforeTabLave (activeName, oldActiveName) {
      // activeName即将离开的标签页
      // oldActiveName即将进入的标签页
      // return false 会阻止进入新标签页
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请先选择商品分类')
        return false
      }
    },
    async tabClicked () {
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'many' } })
        if (res.meta.status !== 200) {
          return this.$message.error('获取动态参数失败!')
        }
        // 将空格分隔的字符串转化为数组
        res.data.forEach(element => {
          element.attr_vals = element.attr_vals.length === 0 ? [] : element.attr_vals.split(' ')
        })
        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'only' } })
        if (res.meta.status !== 200) {
          this.$message.error('获取静态属性失败!')
        }
        this.onlyTableData = res.data
      }
    },
    // 图片预览事件
    handlePreview (file) {
      this.previewPath = file.response.data.url
      this.previewdialogVisible = true
    },
    // 删除图片事件
    handleRemove (file) {
      console.log(file)
      // 1.获取将删除文件临时路径
      const filePath = file.response.data.tmp_path
      // 2.将得到的图片名字,去pics数组中移除
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      this.addForm.pics.splice(i, 1)
    },
    // 上传文件成功时触发
    handleSuccess (response) {
      // 拼接得到一个图片信息对象
      const picInfo = { pic: response.data.tmp_path }
      // 将图片信息push到pics数组中
      this.addForm.pics.push(picInfo)
    },
    // 添加商品
    add () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('清填写必要的表单项')
        }
        // 1.通过验证执行业务添加
        // 2.表单中goods_cat是逗号分隔的列表，此时是数组，要转换
        // 3.this.addForm.goods_cat = this.addForm.goods_cat.join(',')
        // 4.这里不可以直接转换，会报错，因为goods_cat是级联选择框双向绑定的，他只能是数组，所以引起冲突，所以用深拷贝去解决这个冲突
        // 这里使用了一个深拷贝的方法，依赖包lodash是一个js工具库
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = this.addForm.goods_cat.join(',')
        // 处理动态参数
        this.manyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        // 处理静态属性
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs = this.addForm.attrs
        console.log(form)
        // 发起请求添加商品
        // 注意商品名称必须唯一
        const { data: res } = await this.$http.post('goods', form)
        console.log(form)
        if (res.meta.status !== 201) {
          return this.$message.error('添加商品失败!')
        }
        this.$message.success('添加商品成功')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    // 获取三级分类id计算属性
    cateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-steps {
    margin: 15px 0;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.preview {
  width: 100%;
}
.btnTop {
  margin-top: 15px;
}
</style>
